.modal-background {
    position: absolute;
    width : 100%;
    height : 100%;
    background-color: rgb(0, 0, 0, 0.4);
    z-index: 5;
}

.modal {
    background-color: rgb(47, 47, 47);
    border: 1px solid rgb(118, 118, 118);
    border-radius: 8px;
    padding: 12px;
    z-index: 10;
    color : white;
    max-height: 80%;
}

.setting-container {
    width: 200px;
    background-color: rgb(47, 47, 47);
    border: 1px solid rgb(118, 118, 118);
    border-radius: 8px;
    padding: 12px;
    z-index: 10;
    color : white;
}
.setting-container h2 {
    font-size: 24px;
    margin: 4px;
}

.setting-container p {
    font-size: 14px;
    margin : 0px;
    padding: 0px;
}

.setting-container span.material-symbols-outlined {
    font-size: 32px;
}

.setting-container .section {
    margin: 4px 0px 4px 0px;
}

.setting-container input.full {
    width : auto;
}

.setting-container input.small {
    width : 35px;
}